











































































import { Component, Vue, Ref } from "vue-property-decorator";
import CallToAction from "../CallToAction.vue";
import FixedHeader from "../../components/header/FixedHeader.vue";
import { Swiper, SwiperSlide } from "vue-awesome-swiper";
import "swiper/css/swiper.css";
import Api from "../../Api";
@Component({
  components: {
    CallToAction,
    FixedHeader,
    Swiper,
    SwiperSlide,
  },
})
export default class AboutUs extends Vue {
  ourJourney = [{}];
  async beforeCreate() {
    if (window.innerWidth <= 769) {
      this.allowtouch = true;
      this.direction = "horizontal";
    } else {
      this.allowtouch = true;
      this.direction = "vertical";
    }
  }
  async created() {
    await this.fetchJourney();
  }
  static years: string[] = [];
  allowtouch!: boolean;
  direction! : string;
  swiperOption = {
    direction: this.direction,
    loop: true,
    slidesPerView: 1,
    slidesPerGroup: 1,
    spaceBetween: 100,
    allowTouchMove: this.allowtouch,
    pagination: {
      el: ".swiper-pagination",
      renderBullet(index: any, className: any) {
        var year = AboutUs.years;
        return `<span style="font-size:1.2vw" class="${className} swiper-pagination-bullet-custom">
        <span class="bullet-custom"></span><span class="bullet-text">${year[index]}</span></span>`;
      },
      dynamicBullets: true,
      clickable: true,
      // dynamicMainBullets: 3,
    },
  };
  on = {
    resize: () => {
      alert("resized");
      window.innerWidth <= 960
        ? (this.direction = "horizontal")
        : (this.direction = "horizontal");
    },
  };
  ourStory = {
    Content: "",
    Content_Image: "",
  };
  journeyBrief = "";

  async fetchJourney() {
    await fetch(Api.AboutUs.GetAllYears)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        this.ourJourney = data.data;
        for (const id in this.ourJourney) {
          AboutUs.years.push(data.data[id].year);
        }
      })
      .catch((error) => {
        console.log(error);
      });

    await fetch(Api.AboutUs.GetCase)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        this.ourStory = data.data;
        this.ourStory.Content_Image =
          Api.LocalhostImages + this.ourStory.Content_Image;
      })
      .catch((error) => {
        console.log(error);
      });

    await fetch(Api.AboutUs.GetJourneyBrief)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        this.journeyBrief = data.data.Brief;
      })
      .catch((error) => {
        console.log(error);
      });
  }
}
